
type Watcher = (element: HTMLElement) => void;

type ModificationWatcher = {
    onAdd?: Watcher;
    onUpdated?: Watcher;
    onRemove?: Watcher;
}

export class DomWatcher {
    private readonly _observer: MutationObserver;
    private readonly _modifiers: {[p: string]: ModificationWatcher[]} = {};

    constructor() {
        const observer = new MutationObserver((mutations) => {
            for (let i = 0; i < mutations.length; i++) {
                const mutation = mutations[i];

                switch (mutation.type) {
                    case 'childList':
                        for (let j = 0; j < mutation.removedNodes.length; j++) {
                            const node = mutation.removedNodes[j];
                            if (node instanceof HTMLElement) {
                                const element = node as HTMLElement;
                                for (const selector in this._modifiers) {
                                    if (element.matches(selector)) {
                                        const modifier = this._modifiers[selector];
                                        modifier.forEach((object) => {
                                            object.onRemove && object.onRemove(element);
                                        });
                                    }
                                }
                            }
                        }

                        for (let j = 0; j < mutation.addedNodes.length; j++) {
                            const node = mutation.addedNodes[j];
                            if (node instanceof HTMLElement) {
                                const element = node as HTMLElement;
                                for (const selector in this._modifiers) {
                                    if (element.matches(selector)) {
                                        const modifier = this._modifiers[selector];
                                        modifier.forEach((object) => {
                                            object.onAdd && object.onAdd(element);
                                        });
                                    }
                                }
                            }
                        }
                        break;

                    case 'attributes':
                        const element = mutation.target as HTMLElement;
                        const name = `[${mutation.attributeName}]`;
                        for (const selector in this._modifiers) {
                            if (name === selector || element.matches(selector)) {
                                if (mutation.oldValue === null) {
                                    const modifier = this._modifiers[selector];
                                    modifier.forEach((object) => {
                                        object.onAdd && object.onAdd(element);
                                    });

                                } else {
                                    const modifier = this._modifiers[selector];
                                    const valuePresent = element.hasAttribute(mutation.attributeName!);
                                    modifier.forEach((object) => {
                                        if (valuePresent)
                                            object.onUpdated && object.onUpdated(element);

                                        else
                                            object.onRemove && object.onRemove(element);
                                    });

                                }
                            }
                        }
                }
            }
        });

        observer.observe(document.body, {
            childList: true,
            subtree: true,
            attributes: true,
            attributeOldValue: true,
        });
        this._observer = observer;
    }

    public watch(selector: string, object: ModificationWatcher): void {
        const elements = document.querySelectorAll(selector);
        for (let i = 0; i < elements.length; i++) {
            const element = elements[i] as HTMLElement;
            object.onAdd && object.onAdd(element);
        }

        const modifier = this._modifiers[selector] || [];
        modifier.push(object);
        this._modifiers[selector] = modifier;
    }

    public unwatch(selector: string): void {
        delete this._modifiers[selector];
    }

    public isWatching(selector: string): boolean {
        return !!this._modifiers[selector];
    }

    public addEventListener(selector: string, event: string, callback: (event: Event) => void): void {
        this.watch(selector, {
            onAdd: (element) => {
                element.addEventListener(event, callback);
            },
            onRemove: (element) => {
                element.removeEventListener(event, callback);
            }
        });
    }

    public delegateEvent(selector: string, event: string, callback: (event: Event) => void): void {
        document.body.addEventListener(event, (event) => {
            const element = event.target as HTMLElement;
            if (element.matches(selector))
                callback(event);
        });
    }

    public shutdown(): void {
        for (const selector in this._modifiers)
            this.unwatch(selector);

        this._observer.disconnect();
    }
}
