export const pondFormInit = (channel: Channel, watcher: DomWatcher) => {
    pondFocus(channel, watcher);
    pondBlur(channel, watcher);
    pondChange(channel, watcher);
    pondInput(channel, watcher);
    pondSubmit(channel, watcher);
}
